import React, {Component} from  'react';
import {Grid, Cell, List, ListItem, ListItemContent} from 'react-mdl';

class Contact extends Component {
    render(){
        return(
            <div className = "contact-body">
                <Grid className = "contact-grid">
                    <Cell col={6}>
                    <h2>Balogun Sulaimon</h2>
                    <img
                    // src="https://cdn2.iconfinder.com/data/icons/avatar-2/512//Fred_man-512.png"
                    src="https://i.imgur.com/Fl1Jja7.jpg"
                    alt="avatar"
                    style= {{height: '250px'}}
                    />
                    <p style = {{width: '75%', margin: 'auto', paddingTop: '1em'}}>
                    My name is Sulaimon, I am a software Developer, Born in Lagos, Based in Ibadan, Nigeria.
                    I currently work at Royal Niger Company, with a focus on growth and activation
                    </p>
                    </Cell>
                    <Cell col={6}>
                    <h2>Contact Me</h2>
                    <hr/>
                    <div className = "contact-list">
                    <List>
                            <ListItem>
                                <ListItemContent style = {{fontSize: '25px', fontFamily: 'Oxygen'}}>
                                <i className = "fa fa-phone-square" aria-hidden = "true"/>
                                (234)90-3610-4911
                                </ListItemContent>
                            </ListItem>

                            <ListItem>
                                <ListItemContent style = {{fontSize: '25px', fontFamily: 'Oxygen'}}>
                                <i className = "fa fa-fax" aria-hidden = "true"/>
                                (234)80-8751-4916
                                </ListItemContent>
                            
                            </ListItem>

                            <ListItem>
                                <ListItemContent style = {{fontSize: '25px', fontFamily: 'Oxygen'}}>
                                <i className = "fa fa-envelope" aria-hidden = "true"/>
                                Balo.oluwaseyi@gmail.com
                                </ListItemContent>
                            
                            </ListItem>

                            <ListItem>
                                <ListItemContent style = {{fontSize: '25px', fontFamily: 'Oxygen'}}>
                                <i className = "fa fa-Linkedin" aria-hidden = "true"/>
                                
                                </ListItemContent>
                            
                            </ListItem>
                            
                            </List>
                            
                    </div>
                    

                    </Cell>
                </Grid>
            </div>
        )
    }
}

export default Contact;